.projects {
    padding: 25px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-image: linear-gradient(to bottom, black, grey);;
    height: inherit;
}

.pl-texts {
    width: 65%;
}

.title {
    font-size: 50px;
    font-weight: 600;
    color: #d35252;
}

.description {
    margin: 20px 0px;
}

.list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}